(function (V) {
  function onEnterPageExplore () {
    $('.explore-tabs .tab').click(function () {
      showTab($(this));
    });
    showTab($('.explore-tabs .tab:first'));
  }

  function showTab(target) {
    var i = target.index();
    $('.tab-content').hide();
    $('.tab-content:nth(' + i + ')').show();
    target.siblings().removeClass('active');
    target.addClass('active');
  }

  if (V.SCORM.getLocation() === 'explore') {
    onEnterPageExplore();
  }

  V.Pages.explore.onEnter = onEnterPageExplore;
})(window.V);
